import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import HeaderCard from "../../HeaderCard";
import { AxiosMasterGet } from "../../Axios";
import FormLaporanHutangSupplierHead from "./FormLaporanHutangSupplierHead";
import { getToday } from "../../getDate";
import PrintNota from "../../Stocking/Component/PrintNota";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
class FormLaporanHutangSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listNota: [],
      listBayar: [],
      kode_supplier: "",
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "jumlah_hutang",
          text: "Jumlah Hutang",
        },
        {
          dataField: "total_putus_titip",
          text: "Total Putus",
        },
        {
          dataField: "total_denda",
          text: "Total Denda",
        },
        {
          dataField: "total_hutang",
          text: "Total Hutang",
        },
        {
          dataField: "total_pembayaran",
          text: "Total Bayar",
        },
        {
          dataField: "lebih_pembayaran",
          text: "Lebih Bayar",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, index) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.print(index)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      listPenerimaan: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }
  getDetails(faktur) {
    AxiosMasterGet(
      `report/bayar-hutang-supplier/${faktur.kode_supplier}&${faktur.tanggalAwal}&${faktur.tanggalAkhir}&DETAIL&DETAIL`
    ).then((res) =>
      this.setState({
        listNota: res.data[0].detail,
        nama_supplier: res.data[0].nama_supplier,
        listBayar: res.data[0].detail_bayar,
      })
    );
  }

  print(data) {
    // INISIALISASI AUTOTABLE
    const tableRows = [
      [
        "Tunai",
        "Tunai",
        parseFloat(
          this.state.listBayar[data].bayar_cash[0].cash_rp || 0
        ).toLocaleString("id-ID"),
        parseFloat(
          this.state.listBayar[data].bayar_cash[0].harga_emas || 0
        ).toLocaleString("id-ID"),
        "",
        parseFloat(
          this.state.listBayar[data].bayar_cash[0].cash_netto || 0
        ).toLocaleString("id-ID"),
        "",
        "",
      ],
      [
        "Transfer",
        "Transfer",
        parseFloat(
          this.state.listBayar[data].bayar_trf[0].transfer_rp || 0
        ).toLocaleString("id-ID"),
        parseFloat(
          this.state.listBayar[data].bayar_trf[0].harga_emas || 0
        ).toLocaleString("id-ID"),
        "",
        parseFloat(this.state.listBayar[data].bayar_trf[0].transfer_netto || 0),
        "",
        "",
      ],
    ];

    var ciok = this.state.listBayar[data].bayar_ciok;
    ciok.forEach((ticket, i) => {
      const ticketData = [
        "Ciok",
        ticket.kode_jenis,
        ticket.bruto,
        "",
        ticket.kadar_transaksi,
        ticket.netto,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var rongsok = this.state.listBayar[data].bayar_rongsok;
    rongsok.forEach((ticket, i) => {
      const ticketData = [
        "Rongsok",
        ticket.kode_jenis,
        ticket.bruto,
        "",
        ticket.kadar_transaksi,
        ticket.netto,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    var columTable = [
      "Jenis \nPembayaran",
      "Nama \nBarang",
      "Gross \nBayar",
      "Harga",
      "Kadar",
      "Netto",
      "Pkg",
      "Gw+Pkg",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "Tanggal",
      this.state.listNota[data].tanggal,
      "",
      "",
      "Nomor \nPembayaran",
      this.state.listNota[data].no_bon,
      "",
      "",
      "ADMIN",
      getToday(),
      "",
      columTable,
      "NOTA PEMBAYARAN SUPPLIER",
      tableRows
    );
  }

  setLocal(data) {
    localStorage.setItem("mutasiBarang", JSON.stringify(data.barangDetailList));
    this.print(data.tanggal, data.noBon);
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Pembayaran Hutang Supplier" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-lg-12">
                  <FormLaporanHutangSupplierHead
                    onSubmit={(data) => this.getDetails(data)}
                  />
                </div>
                <div className="col-lg-12">
                  {this.state.listPenerimaan ? (
                    <ToolkitProvider
                      bootstrap4
                      keyField={this.state.listPenerimaan}
                      data={this.state.listNota}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="col-lg-12 col-md-12 mt-5">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0 }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            headerClasses="header-table"
                            striped
                            hover
                            bordered={true}
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width="100%" height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanHutangSupplier);
